import { Component, OnInit } from '@angular/core';
import { Ambiente } from '../ambiente';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.page.html',
  styleUrls: ['./dashboard.page.scss'],
})
export class DashboardPage implements OnInit {

  ambientes : Ambiente[] = []
  constructor() { }

  ngOnInit() {
  
    for (let index = 0; index < 2; index++) {
      const indice  =  index + 1
      var ambiente : Ambiente = new Ambiente("ambiente" + indice )
      this.ambientes[index] = ambiente
    }
    var ambiente : Ambiente = new Ambiente("cocina" )
    var ambiente2 : Ambiente = new Ambiente("exterior" )
    this.ambientes.push(ambiente)
    this.ambientes.push(ambiente2)


  }
  cambiarAmbiente(ambiente){
    localStorage.setItem('ambienteseleccionado', ambiente);
    console.log(ambiente)
  }

}
